<template>
  <div>
    <ec-drawer
      title="新增"
      :visible="visible"
      size="small"
      @close="handleCancel"
      :is-confirm="true"
    >
      <ec-container height="auto">
        <ec-container-body>
          <ec-panel>
            <ec-panel-item>
              <ec-form ref="ruleForm" :model="formData" :rules="rules">
                <a-row type="flex" :gutter="12">
                  <a-col :span="12">
                    <a-form-model-item label="品牌编码" prop="brandCode">
                      <a-input
                        v-model="formData.brandCode"
                        placeholder="请输入品牌编码"
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-model-item label="工业企业" prop="induOrgCode">
                      <select-picker
                        v-model="formData.induOrgCode"
                        placeholder="请选择工业企业"
                        title="选择工业单位"
                        :lazy="false"
                        :search="false"
                        :fetchData="onFetchIndustryOrgList"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-model-item label="品牌名称" prop="brandName">
                      <a-input
                        v-model="formData.brandName"
                        placeholder="请输入品牌名称"
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                </a-row>
              </ec-form>
            </ec-panel-item>
          </ec-panel>
        </ec-container-body>
        <ec-container-footer>
          <a-button type="primary" @click="handleSubmit">保存</a-button>
          <a-button @click="handleCancel">关闭</a-button>
        </ec-container-footer>
      </ec-container>
    </ec-drawer>
  </div>
</template>
<script>
import { SelectPicker } from '@euler-material/ecm-ui';
import { treeFormatList } from '@euler-material/ecm-utils';

export default {
  name: 'brandEdit',
  components: { SelectPicker },
  props: {
    industryOrgList: {
      type: Array,
      default: () => [],
    },
    replaceFields: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      visible: false,
      formData: {},
      rules: {
        induOrgCode: { required: true, message: '请输入工业企业' },
        brandName: { required: true, message: '请输入品牌名称' },
      },
    };
  },
  methods: {
    show() {
      this.visible = true;
    },
    // 更新工业单位
    onFetchIndustryOrgList() {
      return new Promise((resolve) => {
        const data = treeFormatList(this.industryOrgList, this.replaceFields);
        resolve(data);
      });
    },
    handleSubmit() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.$apis.apm.baseBrand.save(this.formData).then(() => {
            this.$message.success('操作成功！');
            this.handleCancel();
            this.$emit('onQuery');
          });
        } else {
          this.$message.error('验证不通过');
        }
      });
    },
    // 取消
    handleCancel() {
      this.visible = false;
      this.$refs.ruleForm.resetFields();
    },
  },
};
</script>
<style lang="less" scoped>
.side-info {
  padding: 4px 10px;
  border-radius: 4px;
  border: 1px solid #d5f0ff;
  background: #e6f7ff;
  p {
    margin-bottom: 0;
    color: #8e9496;
    font-size: 14px;
    strong {
      color: #3092fe;
    }
  }
}
</style>
