<template>
  <ec-container height="100%" color-code="001" class="brand" title="卷烟品牌信息维护" show-title>
    <ec-container-body>
      <ec-panel :auto="false">
        <ec-panel-toolbar>
          <ec-query-panel
            :defaultFold="true"
            @query="$refs.xTable.pageAction(1)"
            @reset="resetHandle"
          >
            <ec-query-item label="工业单位">
              <select-picker
                title="选择工业单位"
                :lazy="false"
                :search="false"
                :fetchData="onFetchIndustryOrgList"
                v-model="query.induOrgCode"
              />
            </ec-query-item>
            <template #inner> </template>
          </ec-query-panel>
          <ec-divider />
        </ec-panel-toolbar>
        <ec-panel-body>
          <ec-v-layout :auto="false">
            <!-- 表格工具栏 -->
            <ec-v-item height="auto">
              <ec-panel-toolbar>
                <ec-panel-item-left>
                  <a-button type="primary" @click="handleUpdate">新增</a-button>
                  <a-button type="primary" ghost @click="handleDelete">删除</a-button>
                </ec-panel-item-left>
              </ec-panel-toolbar>
            </ec-v-item>
            <!-- 列表 -->
            <ec-v-item>
              <ec-table
                ref="xTable"
                height="100%"
                :auto="false"
                @page-action="queryHandle"
                :pagination="pagination"
                :data="tableData"
                :loading="loading"
                :edit-config="{ trigger: 'click', mode: 'row' }"
                @checkbox-all="selectAllEvent"
                @checkbox-change="selectRowChange"
                row-select
              >
                <ec-table-col type="checkbox" width="60" align="center"></ec-table-col>
                <ec-table-col type="seq" title="序号" width="50" align="center" />
                <ec-table-col field="induOrgCode" title="工业企业" align="center">
                  <template #default="{ row }">
                    <div>
                      {{
                        row.induOrgCode
                          | getNameBy(industryOrgList, { key: 'bizCode', label: 'orgName' })
                      }}
                    </div>
                  </template>
                </ec-table-col>
                <ec-table-col field="brandCode" title="品牌编码" align="center" />
                <ec-table-col field="brandName" title="品牌名称" align="center" />
              </ec-table>
            </ec-v-item>
          </ec-v-layout>
        </ec-panel-body>
      </ec-panel>
      <business-org-manage-edit
        ref="editRef"
        :industryOrgList="industryOrgList"
        :replaceFields="replaceFields"
        @onQuery="$refs.xTable.pageAction(1)"
      ></business-org-manage-edit>
    </ec-container-body>
  </ec-container>
</template>

<script>
import BusinessOrgManageEdit from './brandEdit.vue';
import { SelectPicker } from '@euler-material/ecm-ui';
import { treeFormatList } from '@euler-material/ecm-utils';

export default {
  name: 'baseBrand',
  components: {
    BusinessOrgManageEdit,
    SelectPicker,
  },
  data() {
    return {
      isEdit: false,
      query: {}, // 查询数据
      loading: false,
      tableData: [], // 表格数据
      pagination: {
        // 分页
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      // 工业单位
      industryOrgList: [],
      // 组织字段重置
      replaceFields: {
        pkey: 'parentBizCode',
        title: 'orgName',
        key: 'bizCode',
        children: 'children',
      },
    };
  },
  computed: {
    bizCode() {
      return this.$store.getters.getAppConfig?.bizCode;
    },
    busiYear() {
      return this.$store.getters.getAppConfig?.yearValueMap?.CENTER_YEAR;
    },
  },
  async created() {
    // 通过路由获取appCode
    const { appCode } = this.$router.history.current.query;
    this.appCode = appCode || 'CM';
    await this.handleQueryIndustryOrgList();
  },
  methods: {
    // 重置
    resetHandle() {
      this.query = {};
      this.$refs.xTable.pageAction(1);
    },
    // 处理查询数据
    handleQueryParams(pageNumber, pageSize) {
      const { pagination } = this;
      this.pagination.pageNumber = pageNumber || pagination.pageNumber;
      this.pagination.pageSize = pageSize || pagination.pageSize;
      const data = {
        ...this.query,
        pageNumber: pagination.pageNumber,
        pageSize: pagination.pageSize,
      };
      return data;
    },
    // 查询
    async queryHandle(pageNumber, pageSize) {
      const data = this.handleQueryParams(pageNumber, pageSize);
      this.loading = true;
      this.$apis.apm.baseBrand
        .query(data)
        .then((res) => {
          this.pagination.total = res.totalRows;
          this.tableData = res.rows;
          this.loading = false;
        })
        .catch((err) => {
          this.loading = false;
          console.log(err);
        });
    },
    // 查询工业单位
    async handleQueryIndustryOrgList() {
      const params = {
        busiYear: this.busiYear,
        orgTypeEk: 'INDUSTRY',
        orgLevelEk: '2',
        parentBizCode: this.bizCode,
      };
      const res = await this.$apis.bim.org.queryBy(params);
      this.industryOrgList = res.rows || [];
    },
    // 更新工业单位
    onFetchIndustryOrgList() {
      return new Promise((resolve) => {
        const data = treeFormatList(this.industryOrgList, this.replaceFields);
        resolve(data);
      });
    },
    // 新增/编辑
    handleUpdate(row) {
      this.$refs.editRef.show(row);
    },
    // 删除
    handleDelete() {
      if (!this.selectedRows || this.selectedRows.length <= 0) {
        this.$message.error('请选择一条数据再删除');
        return;
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认删除选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          this.loading = true;
          const selectList = this.selectedRows.map((item) => {
            return item.bmBrandId;
          });
          console.log(selectList);
          this.$apis.apm.baseBrand
            .delete({ bmBrandIds: selectList })
            .then((res) => {
              console.log(JSON.stringify(res));
              if (res.result) {
                this.loading = false;
                this.$refs.xTable.pageAction(1);
                this.$message.success('操作成功');
              } else {
                this.$message.error('操作失败');
              }
            })
            .catch((error) => {
              this.loading = false;
              if (error) {
                this.$message.error(error.message || '操作失败');
              } else {
                this.$message.error('操作失败');
              }
            });
        },
      });
    },
    // 选中所有列表
    selectAllEvent({ records }) {
      this.selectedRows = records;
    },
    // 选中列表
    selectRowChange({ records }) {
      this.selectedRows = records;
    },
  },
};
</script>
<style lang="less" scoped></style>
